.object-info {
    width: 100%;
    background-color: var(--white);
    box-shadow: 0px 15px 15px rgba(0, 0, 0, 0.04);
    border-radius: 12px;
    display: flex;
    align-items: center;
    color: var(--dark);
    display: grid;
    grid-column-gap: 20px;
    justify-content: space-between;
    grid-template-columns: percent(141, 952) percent(299, 1016) max-content 1fr percent(160, 1016);
    padding: 27px 40px 27px 24px;

    &__type {
        font-size: 18px;
        line-height: math.div(23, 18);
        font-weight: 500;

        @media (max-width:$desktop) {
            order: 1;
            grid-column: 1/2;
        }
    }

    &__parameter {
        font-size: 15px;
        line-height: math.div(19, 15);
        font-weight: 400;

        @media (max-width:$desktop) {
            order: 3;
            grid-column: 1/2;
        }

        @media (max-width:$smallTablet-max) {
            font-size: 12px;
        }
    }

    &__price {
        font-size: 18px;
        line-height: math.div(23, 18);
        font-weight: 500;
        white-space: nowrap;

        @media (max-width:$desktop) {
            order: 2;
            grid-column: 2/3;
            justify-self: end;
        }
    }

    &__tooltips {
        display: flex;

        @media (max-width:$desktop) {
            order: 4;
            grid-column: 2/3;
            justify-self: end;
        }
    }

    &__tooltip {
        &:not(:last-child) {
            margin-right: 15px;
        }
    }

    &__from {
        display: grid;
        grid-template-columns: 40px 1fr;
        grid-column-gap: 12px;
        grid-row-gap: 4px;

        @media (max-width:$desktop) {
            order: 5;
            grid-column: 1/-1;
            padding-top: 20px;
            border-top: 1px solid var(--primary100);
        }

        @media (max-width:$smallTablet-max) {
            grid-column-gap: 8px;
        }
    }

    &__avatar {
        grid-column: 1/2;
        grid-row: 1/3;
        width: 40px;
        height: 40px;
        position: relative;
        border-radius: 50%;

        &::before {
            content: '';
            position: absolute;
            bottom: 0;
            right: 0;
            background-color: var(--green);
            border: 2px solid var(--white);
            display: block;
            width: 12px;
            height: 12px;
            border-radius: 50%;
        }

        img {
            border-radius: 50%;
        }

        @media (max-width:$smallTablet-max) {
            &::before {
                width: 10px;
                height: 10px;
            }
        }
    }

    &__pos {
        grid-column: 2/3;
        grid-row: 1/2;
        font-size: 12px;
        line-height: math.div(15, 12);
        color: var(--primaryTwo);

    }

    &__name {
        grid-column: 2/3;
        grid-row: 2/3;
        font-size: 15px;
        line-height: math.div(19, 15);
        justify-self: start;
        white-space: nowrap;
    }

    @media (max-width:$desktop) {
        grid-template-columns: 1fr max-content;
        grid-row-gap: 20px;
    }

    @media (max-width:$smallTablet-max) {
        padding: 16px;
    }
}
