:root {
  --black: #000;
  --white: #fff;
  --primary600: #32425F;
  --primary500: #3961AA;
  --primary400: #2683F2;
  --primary300: #78B4FB;
  --primary200: #CBE3FF;
  --primary100: #EBF1FB;
  --primaryTwo: #7A7FAA;
  --dark: #161616;
  --red: #FF4848;
  --green: #24CA8E;
  --yellow: #FFDA16;
  --pink: #FA8080;
  --orange: #D86D00;
  --gray: #3D3D3D;
  --blue: #006CEE;
}

/* stylelint-disable */
.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}
.btn-reset:focus {
  outline: none;
}

.btn {
  transition: color 0.4s ease-in-out, background-color 0.4s ease-in-out, border 0.4s ease-in-out;
}

.btn-primary {
  height: 48px;
  font-family: "CoFoSans", sans-serif;
  border-radius: 16px;
  background-color: var(--blue);
  color: var(--white);
  font-size: 15px;
  line-height: 1.2666666667;
  font-weight: 500;
  padding: 14.5px 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn-primary:hover {
  background-color: var(--primary500);
}
.btn-primary:active {
  background-color: var(--primary600);
}
.btn-primary:disabled {
  background-color: #F2F2F2;
  box-shadow: none;
  pointer-events: none;
  cursor: default;
}

.btn-secondary {
  height: 48px;
  font-family: "CoFoSans", sans-serif;
  border-radius: 16px;
  color: var(--blue);
  border: 1px solid currentColor;
  font-size: 15px;
  line-height: 1.2666666667;
  font-weight: 500;
  padding: 13.5px 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn-secondary:hover {
  color: var(--primary500);
}
.btn-secondary:active {
  color: var(--primary600);
}
.btn-secondary:disabled {
  color: #F2F2F2;
  pointer-events: none;
  cursor: default;
}

.checkbox {
  position: relative;
}
.checkbox__input {
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
}
.checkbox__input:checked + .checkbox__label {
  background-color: var(--primary200);
}
.checkbox__input:checked + .checkbox__label .checkbox__text {
  color: var(--blue);
}
@media (max-width: 1112px) {
  .checkbox__input:checked + .checkbox__label {
    background-color: var(--blue);
  }
  .checkbox__input:checked + .checkbox__label .checkbox__text {
    color: var(--white);
  }
}
.checkbox__label {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  position: relative;
  height: 23px;
  padding: 0 10px;
  background-color: var(--white);
  border-radius: 8px;
  transition: background-color 0.4s ease-in-out;
}
@media (max-width: 1112px) {
  .checkbox__label {
    height: 32px;
    padding: 0 13px;
    background-color: var(--primary100);
    border-radius: 7px;
  }
}
.checkbox__text {
  color: var(--gray);
  font-size: 12px;
  line-height: 1.25;
  font-weight: 400;
  transition: color 0.4s ease-in-out;
}
@media (max-width: 1112px) {
  .checkbox__text {
    font-size: 13px;
    line-height: 100%;
  }
}

.checkbox-secondary {
  position: relative;
}
.checkbox-secondary__input {
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
}
.checkbox-secondary__input:focus + .checkbox-secondary__label::before {
  background-color: var(--primary100);
  border: 1px solid var(--primary300);
}
.checkbox-secondary__input:checked + .checkbox-secondary__label::before {
  background-color: var(--blue);
  border: 1px solid var(--blue);
}
.checkbox-secondary__input:checked + .checkbox-secondary__label::after {
  opacity: 1;
  visibility: visible;
}
.checkbox-secondary__label {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
.checkbox-secondary__label::before {
  content: "";
  width: 15px;
  height: 15px;
  background-color: var(--primary100);
  border: 1px solid var(--primaryTwo);
  border-radius: 2px;
  transition: background-color 0.4s ease-in-out;
}
.checkbox-secondary__label::after {
  content: "";
  width: 15px;
  height: 15px;
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  transition: opacity 0.4s ease-in-out, visibility 0.4s ease-in-out;
  background-image: url("../img/check-mark.svg");
  background-repeat: no-repeat;
  background-size: 9px 6px;
  background-position: center;
}
.checkbox-secondary__text {
  margin-left: 10px;
  color: var(--gray);
  font-size: 15px;
  line-height: 1.2666666667;
  font-weight: 400;
}

@font-face {
