html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  font-weight: 400;
  font-size: inherit;
}

.page {
  height: 100%;
  font-family: "CoFoSans", sans-serif;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 15px;
  line-height: 100%;
  background-color: #fff;
  color: #3D3D3D;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
  display: inline-block;
}

.site-container {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.dis-scroll {
  overflow: hidden;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.page--ios .dis-scroll {
  position: relative;
}

.container {
  margin: 0 auto;
  padding: 0 16px;
  max-width: 1112px;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.title-1 {
  font-weight: 500;
  font-size: 32px;
  line-height: 1.25;
  color: var(--dark);
}
@media (max-width: 1112px) {
  .title-1 {
    font-size: 26px;
  }
}
@media (max-width: 768px) {
  .title-1 {
    font-size: 20px;
  }
}

.title-2 {
  font-weight: 500;
  font-size: 24px;
  line-height: 1.25;
  color: var(--dark);
}

.title-3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 1.2777777778;
  color: var(--dark);
}

.title-4 {
