}

.main-tooltip {
  position: relative;
}
.main-tooltip__target {
  cursor: pointer;
  border-radius: 8px;
  width: 27px;
  height: 27px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-tooltip__target svg {
  width: 15px;
  height: 15px;
}
.main-tooltip__content {
  width: 185px;
  background-color: var(--white);
  border: 1px solid rgba(122, 127, 170, 0.1);
  box-shadow: 0px 15px 15px rgba(0, 0, 0, 0.04);
  border-radius: 12px;
  color: var(--primaryTwo);
  padding: 20px 18px;
  font-size: 14px;
  line-height: 1.2857142857;
  position: absolute;
  bottom: 100%;
  left: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.4s ease-in-out, visibility 0.4s ease-in-out;
}
@media (max-width: 1112px) {
  .main-tooltip__content {
    right: 0;
    left: auto;
  }
}
.main-tooltip--bank .main-tooltip__target {
  background: rgba(36, 202, 142, 0.2);
}
.main-tooltip--bank .main-tooltip__target svg {
  fill: #24CA8E;
}
.main-tooltip--budget .main-tooltip__target {
  background: rgba(255, 227, 66, 0.2);
}
.main-tooltip--budget .main-tooltip__target svg {
  fill: #FFE342;
}
.main-tooltip--dollar .main-tooltip__target {
  background: rgba(252, 147, 147, 0.2);
}
.main-tooltip--dollar .main-tooltip__target svg {
  fill: #FC9393;
}
.main-tooltip--bolt .main-tooltip__target {
  background: rgba(255, 27, 27, 0.2);
}
.main-tooltip--bolt .main-tooltip__target svg {
  fill: #FF1B1B;
}
.main-tooltip--calculator .main-tooltip__target {
  background: rgba(148, 61, 189, 0.2);
}
.main-tooltip--calculator .main-tooltip__target svg {
  fill: #943DBD;
}
.main-tooltip:hover .main-tooltip__content {
  opacity: 1;
  visibility: visible;
}

.object-info {
  width: 100%;
  background-color: var(--white);
  box-shadow: 0px 15px 15px rgba(0, 0, 0, 0.04);
  border-radius: 12px;
  display: flex;
  align-items: center;
  color: var(--dark);
  display: grid;
  grid-column-gap: 20px;
  justify-content: space-between;
  grid-template-columns: 14.8109243697% 29.4291338583% max-content 1fr 15.7480314961%;
  padding: 27px 40px 27px 24px;
}
.object-info__type {
  font-size: 18px;
  line-height: 1.2777777778;
  font-weight: 500;
}
@media (max-width: 1112px) {
  .object-info__type {
    order: 1;
    grid-column: 1/2;
  }
}
.object-info__parameter {
  font-size: 15px;
  line-height: 1.2666666667;
  font-weight: 400;
}
@media (max-width: 1112px) {
  .object-info__parameter {
    order: 3;
    grid-column: 1/2;
  }
}
@media (max-width: 768px) {
  .object-info__parameter {
    font-size: 12px;
  }
}
.object-info__price {
  font-size: 18px;
  line-height: 1.2777777778;
  font-weight: 500;
  white-space: nowrap;
}
@media (max-width: 1112px) {
  .object-info__price {
    order: 2;
    grid-column: 2/3;
    justify-self: end;
  }
}
.object-info__tooltips {
  display: flex;
}
@media (max-width: 1112px) {
  .object-info__tooltips {
    order: 4;
    grid-column: 2/3;
    justify-self: end;
  }
}
.object-info__tooltip:not(:last-child) {
  margin-right: 15px;
}
.object-info__from {
  display: grid;
  grid-template-columns: 40px 1fr;
  grid-column-gap: 12px;
  grid-row-gap: 4px;
}
@media (max-width: 1112px) {
  .object-info__from {
    order: 5;
    grid-column: 1/-1;
    padding-top: 20px;
    border-top: 1px solid var(--primary100);
  }
}
@media (max-width: 768px) {
  .object-info__from {
    grid-column-gap: 8px;
  }
}
.object-info__avatar {
  grid-column: 1/2;
  grid-row: 1/3;
  width: 40px;
  height: 40px;
  position: relative;
  border-radius: 50%;
}
.object-info__avatar::before {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  background-color: var(--green);
  border: 2px solid var(--white);
  display: block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
}
.object-info__avatar img {
  border-radius: 50%;
}
@media (max-width: 768px) {
  .object-info__avatar::before {
    width: 10px;
    height: 10px;
