}
.object-info__pos {
  grid-column: 2/3;
  grid-row: 1/2;
  font-size: 12px;
  line-height: 1.25;
  color: var(--primaryTwo);
}
.object-info__name {
  grid-column: 2/3;
  grid-row: 2/3;
  font-size: 15px;
  line-height: 1.2666666667;
  justify-self: start;
  white-space: nowrap;
}
@media (max-width: 1112px) {
  .object-info {
    grid-template-columns: 1fr max-content;
    grid-row-gap: 20px;
  }
}
@media (max-width: 768px) {
  .object-info {
    padding: 16px;
  }
}

