.pagination {
  --width-item: 24px;
  --height-item: 23px;
  --gap-item: 10px;
  display: flex;
  align-items: center;
}
.pagination__list {
  display: flex;
  align-items: center;
}
.pagination__item {
  min-width: var(--width-item);
  height: var(--height-item);
  border-radius: 7px;
  background-color: var(--white);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 13px;
