.select-primary {
  position: relative;
  cursor: pointer;
}
.select-primary__name {
  position: absolute;
  z-index: 2;
  top: 8.5px;
  left: 16px;
  color: var(--primaryTwo);
  font-size: 9px;
  line-height: 100%;
  font-weight: 400;
}
.select-primary .choices {
  background-color: var(--primary100);
  overflow: visible;
  padding: 20.5px 16px 8.5px 16px;
  border-radius: 12px;
  transition: border-radius 0.4s ease-in-out;
}
.select-primary .choices::after {
