.nav-dropdown .nav__link {
  transition: border-bottom 0.4s ease-in-out;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
}
.nav-dropdown .nav__link::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 2px;
  background-color: var(--blue);
  transition: width 0.4s ease-in-out;
